:root {
  --color-background: #eeeeee;
  --color-content-background: #FFFFFF;
  --color-accent: #ffc107;
  --color-content: #000000;
  --content-max-width: 722px; }

:root {
  --color-background: #424242;
  --color-content-background: #616161;
  --color-accent: #f9a825;
  --color-content: #FAFAFA;
  --content-max-width: 722px; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #424242;
  background-color: var(--color-background);
  color: #FAFAFA;
  color: var(--color-content); }

#app, main {
  width: 100vw;
  height: 100vh;
  overflow: hidden; }

.app-header {
  width: 100%;
  height: 128px;
  position: fixed;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center; }
  .app-header .app-header--content {
    width: 100%;
    max-width: 722px;
    max-width: var(--content-max-width);
    margin: 0 auto;
    padding: 0 16px;
    box-sizing: border-box; }

.app-name {
  width: 100%;
  height: 56px;
  line-height: 56px;
  text-align: center;
  font-weight: bold;
  font-size: 36px;
  font-family: cursive; }

.app-content {
  height: 100vh;
  overflow-y: auto; }
  .app-content .app-content--content {
    width: 100%;
    max-width: 722px;
    max-width: var(--content-max-width);
    margin: 0 auto;
    padding: 0 16px;
    box-sizing: border-box;
    margin-top: 128px;
    margin-bottom: 128px; }

.note-list {
  display: grid;
  grid-gap: 24px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-auto-rows: 40px; }

.note {
  border-radius: 8px;
  background-color: #616161;
  background-color: var(--color-content-background);
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
  grid-row-end: span 4;
  display: inline-block;
  transition: -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out; }
  .note.selected {
    background-color: #eeeeee; }

.app-actions {
  width: 100%;
  height: 64px;
  position: fixed;
  bottom: 32px; }
  .app-actions .app-actions--content {
    width: 100%;
    max-width: 722px;
    max-width: var(--content-max-width);
    margin: 0 auto;
    padding: 0 16px;
    box-sizing: border-box;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center; }
    .app-actions .app-actions--content .action {
      margin-left: 16px;
      box-shadow: 4px 6px 6px rgba(0, 0, 0, 0.15);
      cursor: pointer;
      display: -webkit-flex;
      display: flex;
      -webkit-align-items: center;
              align-items: center;
      -webkit-justify-content: center;
              justify-content: center; }
      .app-actions .app-actions--content .action:first-of-type {
        margin-left: 0; }

.action.action-button {
  width: 56px;
  height: 56px;
  border-radius: 28px;
  background-color: #f9a825;
  background-color: var(--color-accent); }

.action.action-bar {
  width: 100%;
  height: 56px;
  border-radius: 8px;
  background-color: #f9a825;
  background-color: var(--color-accent); }
